import { AppBar as MuiAppBar, IconButton, Toolbar, Typography } from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import React from 'react';
import { connect } from 'react-redux';
import { toast, ToastContainer } from "react-toastify";

const Bar = props => {
  const handleCopy = () => {
    const mediumElement = document.querySelector('#medium');
    // 
    const range = document.createRange();
    range.selectNode(mediumElement);

    // 
    window.getSelection().removeAllRanges();
    // 
    window.getSelection().addRange(range);

    // 
    try {
      document.execCommand('copy');
      toast(() => (
        <div>
          
          <a 
            href="https://medium.com/new-story" 
            target="_blank" 
            rel="noopener noreferrer"
            style={{ color: '#2196F3', marginLeft: '8px' }}
          >
             Medium 
          </a>
        </div>
      ), {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
        pauseOnHover: true,
      });
    } catch (err) {
      console.error(':', err);
    }

    // 
    window.getSelection().removeAllRanges();
  };

  return (
    <div>
      <ToastContainer 
        position="top-right"
        style={{ top: '70px' }}
      />
      <MuiAppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Markdown to Medium
          </Typography>
          <IconButton 
            color="inherit" 
            onClick={handleCopy} 
            title=""
          >
            <ContentCopyIcon />
          </IconButton>
        </Toolbar>
      </MuiAppBar>
    </div>
  );
};

export default connect(state => ({
  menuactive: state.menuactive
}))(Bar);
